button{
    padding: 0 1.1rem;
}
i{
    text-align: center;
    padding: 0 1.1rem;
}
#quote-div{
        width: 430px;
    }
#new-quote:hover{
    transform: scale(1.1);
}
#new-quote:active{
    outline: none;
}
#new-quote:focus{
    outline: none;
}


@media(orientation: portrait){
    #quote-div{
        width: 65vw;
    }
   
}